import React from "react";
import { Box, Card, CardContent, Typography } from "@mui/material";

const Calendar = () => {
  return (
    <Box>
      <Card variant="outlined">
        <CardContent>
        <Typography variant="h3">Calendario</Typography>
          <Box
            sx={{
              overflow: {
                xs: "auto",
                sm: "unset",
              },
            }}
          >

          </Box>
        </CardContent>
      </Card>
    </Box>
  );
};

export default Calendar;
